//import logo from './logo.svg';
import DefaultLayout from './DefaultLayout';
import React from "react";
import "./css/Home.css"


var FB = null;
// Event is emitted in index.html
document.addEventListener("fb_init", (e) => {
    FB = e.detail;
});

class Home extends React.Component {

    render() {
        return (
            <DefaultLayout>
                <div>
                    <p>
                    The Cahaba Distance Project is a high-performance running group based in Birmingham, Alabama.
                    </p>
                    <div className="fb-wrapper">
                        <div className="fb-page"
                            data-width="180px"
                            data-href="https://www.facebook.com/Cahaba-Distance-Project-159904641087413"
                            data-tabs="timeline"
                            data-small-header="true"
                            data-hide-cover="true"
                            data-show-facepile="false"
                        >
                        </div>
                    </div>
                </div>
            </DefaultLayout>
        )
    }

    componentDidMount() {
        if (FB) FB.XFBML.parse();
    }

}


export default Home;
